/*
 *  USP's
 *    Styles for the usp's section.
 *
 *  Version:
 *    1.0.0 - 2021/10/15
 */

.c-usps {
  padding: 5.3125rem 2.75rem 5.625rem;
  background-color: $s-color--background-focus;

  /*** Media Queries ***/

  @include t-media-query($s-medium-up) {
    padding: 4.3875rem 2.75rem 5.4375rem;
  }

  @include t-media-query($s-xlarge-up) {
    padding: 7.3125rem $s-gutter--edge-medium 9.0625rem;
  }
}