/*
 *  USP's Object
 *    Styles for the usp's snippet.
 *
 *  Version:
 *    1.0.0 - 2021/10/15
 */

.o-usps {
  max-width: 75rem;
  margin: 0 auto;

  &__pretitle, &__title {
    margin-bottom: 0.9375rem;
    text-align: center;
  }

  &__cta-container {
    margin-bottom: 2.5rem;
    text-align: center;
  }

  &__usps {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  &__usp {
    display: flex;
    max-width: 18.75rem;
    margin-bottom: 2rem;

    &:last-child {
      margin-bottom: 0;
    }
  }

  &__icon {
    width: 100%;
    max-height: 2.25rem;

    &-container {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 2.5rem;
      margin-right: 1.375rem;
    }
  }

  &__content {
    flex-grow: 1;
  }

  &__line-1 {
    margin-bottom: 0.125rem;
    font-weight: 500;

    &:last-child {
      margin-bottom: 0;
    }
  }

  &__line-2 {
    margin-bottom: 0;
  }

  /*** Media Queries ***/

  @include t-media-query($s-small-up) {
    &__usps {
      flex-direction: row;
      justify-content: center;
      align-items: initial;
    }

    &__usp {
      margin-right: 2rem;
      margin-bottom: 0;

      &:last-child {
        margin-right: 0;
      }
    }
  }

  @include t-media-query($s-medium-up) {
    &__usp {
      margin-right: 5.8125rem;
      max-width: none;
    }

    &__icon {
      max-height: 3.25rem;

      &-container {
        width: 3.5rem;
      }
    }
  }

  @include t-media-query($s-xlarge-up) {
    &__pretitle {
      margin-bottom: 1rem;
    }

    &__cta-container {
      margin-bottom: 3.8125rem;
    }

    &__usps {
      justify-content: space-between;
    }

    &__usp {
      margin-right: 9.6875rem;
    }
  }
}
